<template lang="pug">
  .h5-editor.clear
    .opreate-block.fl
      button(@click="addElement(1)" class="btn-style-1") 添加文本元素
      button(@click="addImageElement()" class="btn-style-1") 添加图片元素
      //- button(@click="addElement(100)") 添加100元素
      //- button(@click="addElement(200)") 添加200元素
      button(@click="undo", :disabled="!isVuexCaptureHaveUndo" style="margin-left: 10px") 撤销
      button(@click="redo", :disabled="!isVuexCaptureHaveRedo" style="margin-left: 10px") 重做
      //- select(@change="colorChange", v-model="colorModel" style="margin-left: 20px")
      //-    option(v-for="(item, index) in colorOptions", :key="index", :value="item.value") {{item.label}}
      //- select(@change="rotateChange", v-model="rotateModel" style="margin-left: 20px")
      //-    option(v-for="(item, index) in rotateOptions", :key="index", :value="item.value") {{item.label}}
    .statistics
      span (添加元素可以拖拽可以旋转)
      span 元素总数量： {{elementList.length}}
    component(:is="renderComonentType(item)" v-for="(item, index) in elementList", :key="item.elementId", :element-data="item")
    maka-operate
    maka-element-hover
    text-editor
    img-editor
</template>
<script>
import index from './index'

export default {
  ...index
}
</script>
<style lang="stylus" scoped>
.h5-editor
  position relative
  padding-top 50px
  width 100%
  height 100%
  box-sizing border-box
  border 1px solid #ccc
  .opreate-block
    position absolute
    top 0
    left 0
    height 50px
    width 100%
    box-shadow 1px 1px 2px #ccc
  .statistics
    position absolute
    top 5px
    right 5px
</style>



